	.page {
		width: 1400px;
	}

	.cat_item {
		text-align: center;
		width: auto !important;
		span {
			position: static !important;
			display: block !important;
			background: none !important;
			margin-left: 0 !important;
			width: auto !important;
			font-size: 14px;
			color: #808080 !important;
			&:before {
				display: none !important;
			}
		}
	}

	.offer {
		.offer__text {
			padding: 0 25px 0 36px;
		}
		.offer__year {
			font-size: 37px;
			padding-top: 21px;
		}
		.offer__title {
			font-size: 44px;
		}
		.offer__more {
			font-size: 21px;
		}
	}

	.entry.entry_big {
		.entry__text {
			padding: 33px 40px 69px 30px;
			.entry__title {
				font-size: 30px;
			}
		}
	}

	// Slider
	.superslider {
		height: 89.9vh;
		.superslider__text {
			padding: 0 71px;
		}
	}

	.b-catalog {
		.superslider {
			max-width: 1400px;
			margin: 0 auto;
			height: 125.5vh;
			.superslider__next {
				right: 30px;
			}
			.superslider__playpause {
				right: 30px;
			}
			.superslider__playpause {
				right: 30px;
			}
			.superslider__pagi {
				right: 50px;
			}
		}
		.logoslider {
			margin-bottom: 29px;
			margin-top: 25px;
			.logoslider__next,
			.logoslider__prev {
				top: 50%;
			}
		}
	}

	// Logoslider

	.logoslider {
		margin-top: 24px;
		img {
			margin: 0 30px 0 0;
		}
	}


	//////////////Catalog Page

	// Choosed List

	.choosed_list_container {
		position: fixed;
		bottom: 0;
		right: 90px;
		z-index: 100;
		background-color: #000000;
		-webkit-border-radius: 12px 12px 0 0;
		-moz-border-radius: 12px 12px 0 0;
		border-radius: 12px 12px 0 0;
		width: 700px;
		padding: 0 12px 0 10px;
		&.collapsed {
			.choosed_list_content {
				display: none;
			}
		}
		.choosed_list_header{
			padding: 17px 0 12px;
			.heading {
				color: #E5E5E5;
				font-size: 16px;
				display: inline-block;
				margin-left: 47px;
				word-spacing: 2px;
				float: left;
			}
			.actions {
				float: right;
				margin-right: 22px;
				margin-top: 2px;
				a {
					display: inline-block;
					width: 20px;
					height: 14px;
					border-style: solid;
					border-width: 1px;
					margin-left: 27px;
					&.action_collapse {
						border-width: 0;
						border-bottom-width: 3px;
						border-bottom-color: #E5E5E5;
					}
					&.action_show {
						border-color: #E5E5E5;
						border-top-width: 3px;
					}
				}
			}
		}
		.choosed_list_content {
			background-color: #ffffff;
			padding: 30px 42px 30px 45px;
			.choosed_product {
				border-bottom: 1px solid #b2b2b2;
				padding-bottom: 4px;
				margin-bottom: 1px;
				.choosed_product_pic {
					width: 62px;
					height: 54px;
					float: left;
					text-align: center;
					margin-right: 8px;
					padding: 2px;
					img {
						display: inline-block;
						max-height: 100%;
						max-width: 100%;
					}
				}
				.choosed_product_text {
					width: 450px;
					float: left;
					padding-top: 8px;
					p {
						font-size: 18px;
						color: #B2004D;
						line-height: 1;
					}
					span {
						font-size: 14px;
						color: #383838;
					}
				}
				.choosed_product_act {
					width: 36px;
					float: right;
					margin-top: 10px;
					.checked, .checked_plus {
						width: 36px;
						height: 36px;
						background: url(../img/icons/checked2.png);
						cursor: pointer;
						position: relative;
						span {
							display: none;
							position: absolute;
							background: #333333;
							font-size: 11px;
							color: #E5E5E5;
							left: 20px;
							top: 10px;
							padding: 2px 4px;
						}
						&:hover span {
							display: inline-block;
						}
					}
					.checked_plus {
						background: url(../img/icons/checked3.png);
					}
				}
			}
		}
	}

	//\\ End Choosed list

	///////////////End catalog Page

	//\\Slider

	.footer {
		.footer__in {
			padding: 65px 0 87px 0;
		}
		.footer__nav {
			left: 101px;
			font-size: 21px;
			ul {
				line-height: 24px;
				position: relative;
				top: -4px;
				li {
					display: inline-block;
					width: 100%;
				}
			}
		}
		.footer__contacts {
			left: 98px;
			padding-left: 19px;
			font-size: 14px;
			h4 {
				font-size: 21px;
				line-height: 0.8;
				margin-bottom: 11px;
			}
			p {
				line-height: 1.25;
			}
		}
	}
	.warning {
		background-size: auto;
		max-width: none;

	}
